h1,h2,h4{
    font-family: 'Acme', sans-serif;;
}

h3,h5{
    font-family: 'Roboto', sans-serif;
}

#webTitle{
    font-family: 'Acme', sans-serif;
    font-size: 100px;
}

.jumbotron{
    background-color:gold;
}

#studyPic{
    background-image: -webkit-gradient(linear,left top, right bottom,from(rgba(246, 247, 247, 0.7)),to(rgba(241, 245, 247, 0.7))),url("../../img/studying.jpg");
    background-image: -webkit-linear-gradient(left top,rgba(246, 247, 247, 0.7),rgba(241, 245, 247, 0.7)),url("../../img/studying.jpg");
    background-image: -o-linear-gradient(left top,rgba(246, 247, 247, 0.7),rgba(241, 245, 247, 0.7)),url("../../img/studying.jpg");
    background-image: linear-gradient(to right bottom,rgba(246, 247, 247, 0.7),rgba(241, 245, 247, 0.7)),url("../../img/studying.jpg");
    height: 500px;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#map-col, #jumbo-col {
    padding: 0px;
}
#studyPic-col{
    padding: 0px;
}

#mapRow{
    height: 400px;
}
#mapColumn{
    padding-right: 0px;
}

.message{
    font-family: 'Roboto', sans-serif;
}


